<template>
   <section  id="roleSetting">
      <span class="title">{{$t('role.role')}}</span>
      <el-col :span="24" style="padding-bottom: 0px" class="toolbar">
        <el-form :inline="true" :model="userForm">
            <el-form-item>
                 <el-button class="warning newAdd" v-on:click="newAdd=true">{{$t('role.do')}}</el-button>
            </el-form-item>
        </el-form>
      </el-col>
      <el-table :data="roleList"   v-loading="userLoading" style="width: 100%;" :row-class-name="tableRowClassName" >
           <el-table-column prop="authgroupName"  :label="$t('role.thead[0]')">
            </el-table-column>
            <el-table-column prop="ctime" :label="$t('role.thead[1]')">
            </el-table-column>
            <el-table-column prop="utime"  :label="$t('role.thead[2]')">
            </el-table-column>
            <el-table-column   :label="$t('role.thead[3]')">
              <template slot-scope="scope">
                  <el-button
                    size="mini"
                    @click="showAuthoritiesUnderRole(scope.$index,scope.row)">{{$t('allot')}}</el-button>
                    
                </template>
            </el-table-column>
            <!-- <el-table-column prop="edit" :label="$t('role.thead[4]')">
            </el-table-column> -->
            <el-table-column :label="$t('role.thead[4]')">
                <template slot-scope="scope">
                  <el-button
                    size="mini"
                    @click="fixDate(scope.$index,scope.row)">{{$t('xiuGai')}}</el-button>
                    <!-- <el-button size="mini"  @click="deleteRoles(scope.$index,scope.row)">{{$t('delete')}}</el-button> -->
                    <el-button type="text" @click="open2(scope.$index,scope.row)">{{$t('delete')}}</el-button>
                </template>
            </el-table-column>
      </el-table>


<!-- 修改弹框 -->
<el-dialog :title="$t('RoleChange')" :visible.sync="fixRole" id="fixRoledd">
    <el-form :model="fixRoleForm">
    <el-form-item :label="$t('oldRole')" >
      <el-input v-model="fixRoleForm.oldRole" auto-complete="off"></el-input>
    </el-form-item>
    <el-form-item :label="$t('newRole')" >
      <el-input v-model="fixRoleForm.newRole" auto-complete="off"></el-input>
    </el-form-item>
    
  </el-form>
  <div slot="footer" class="dialog-footer">
    <el-button @click="fixRole = false" class="exit">{{$t('cancel')}}</el-button>
    <el-button type="primary" @click="updateRole" class="sureSure">{{$t('confirm')}}</el-button>
  </div>
</el-dialog>


<el-dialog title="分配权限" :visible.sync="allotShow"  id="allotPower">
 

    
        <el-checkbox-group v-model="allotData2">
          <template v-for="(item,index) in allotData">
             <el-checkbox :label="item.authId" @change="changeFun(item)" :key="index" v-show="index>13 && index !=23 && index != 45">{{item.authName}}</el-checkbox>
          </template>
       
        
        </el-checkbox-group>
    
    <div slot="footer" class="dialog-footer">
    <el-button @click="allotShow = false" class="exit">{{$t('cancel')}}</el-button>
    <el-button type="primary" @click="assignAuthorities4Role" class="sureSure">{{$t('confirm')}}</el-button>
  </div>
</el-dialog>

<!-- 新增弹窗 -->
<el-dialog :title="$t('newAdd')" :visible.sync="newAdd" id="newRoleDig">
  <p>{{$t('NewRoleName')}}<input class="search" type="text" v-model = "createRo"></p>
  <div slot="footer" class="dialog-footer">
    <el-button @click="newAdd = false" class="exit">{{$t('cancel')}}</el-button>
    <el-button type="primary" @click="createRole" class="sureSure">{{$t('confirm')}}</el-button>
  </div>
</el-dialog>


      <div class="page" v-show="total >10">
        <el-col :span="24" class="toolbar  ">
           <el-pagination
            background
            layout="prev, pager, next"
            :total="total" @current-change="handleCurrentChange">
          </el-pagination>
        </el-col>
      </div>
   </section>
</template>
   
<script>
import {ajax,dateStr2GmtTimeStamp,getDataYear} from '../../common/js/common.js'
export default {
   data(){
     return{
      allotData2:[],
      checkList: [],
      createRo:"",
      allotShow:false,
      allotData:"",
      fixRole:false,
      fixRoleForm:{
        oldRole:'',
        newRole:'',
      },
      updateRoleData:{
        roleId:'',
        roleName:''
      },
      roleId:"" ,
      fix:false,
      newAdd:false,
       userForm:{
        
       },
       
       userLoading:false,
       roleList:[],
       roleDate:{
        page:1,
        rowNum:"10",
        // roleName:"",
        startTime:"",
        endTime:"",
       },
       total:0,
     }
   },
   methods:{
    // 查询角色信息
     queryRoles:function(){
        ajax(this,this.ApiConfig.userManagement.role.queryRoles,this.roleDate,(res) => {
          console.log(res)
        this.roleList = res.data.rows;
        this.total = res.data.total;
        let arr = this.roleList;
                  arr.forEach((item,i,arr) => {
                     arr[i].ctime = getDataYear('Y-M-D h:m:s',arr[i].ctime);
                     arr[i].utime = getDataYear('Y-M-D h:m:s',arr[i].utime);
                  }); 
        this.userLoading = false;

        },"POST")
     },
    // 创建后台角色
     createRole:function(){
        let  data = {
            roleName:this.createRo,
        };
        ajax(this,this.ApiConfig.userManagement.role.createRole,data,(res) =>{
          console.log(res)
          this.newAdd = false
          this.queryRoles();
          this.createRo = "";
        },"POST")
     },
     open2:function(index,row) {
        this.$confirm('此操作将永久删除该角色, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.deleteRoles(index,row);
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });
      },

    // 删除后台角色信息
     deleteRoles:function(index,row){
          console.log(row)
          
          let roleIds = [];
          roleIds.push(row.authgroupId)
          let data = {
            roleIds:roleIds,
          }
          ajax(this,this.ApiConfig.userManagement.role.deleteRoles,data,(res) =>{
            console.log(res);
            this.queryRoles();
          },"POST")
     },
    //修改角色信息 ①拿数据
     fixDate:function(index,row){
        this.fixRole = true;
        console.log(row)
        this.fixRoleForm.oldRole = row.authgroupName;
        
        this.updateRoleData.roleId =row.authgroupId;
     },
    // 修改角色信息 ②发请求
     updateRole:function(){
          this.updateRoleData.roleName = this.fixRoleForm.newRole;
          ajax(this,this.ApiConfig.userManagement.role.updateRole,this.updateRoleData,(res) =>{
            console.log(res)
            this.fixRole = false;
            this.queryRoles();
            
          },"POST")

     },
    // 显示角色下的权限信息
     showAuthoritiesUnderRole:function(index,row){
      this.allotData2=[];
        let data = {
          roleId:row.authgroupId,
        };
        this.roleId =row.authgroupId;//这个是给下面传参的时候用
        ajax(this,this.ApiConfig.userManagement.role.showAuthoritiesUnderRole,data,(res) => {
          console.log(res)
          // console.log(res.data.length)
          this.allotData =res.data;
          this.allotShow = true;

          console.log(this.allotData)
            for(var i=0;i<this.allotData.length;i++){
              if(this.allotData[i].isSelected ==1){
                this.allotData2.push(this.allotData[i].authId)  //点击分配的时候  已经选中的数组allotData2
              }
              
            }
          
        },"POST")
     },
     //选择权限(isSelected,authId,authName)
      changeFun:function(target) {
        if(target.isSelected ==0){
          target.isSelected =1;
        }else{
          target.isSelected =0;
        }
        console.log(target)
        this.allotData[target.authId-1] =target
         
        
          
        

    },
    // 角色分配权限(参数 roleId (角色id) authIds(数组形式))

     assignAuthorities4Role:function(){       //  点击确定做的事情
         this.checkList = [];
         console.log(this.checkList+"这是前面的checkList")
         
      for(var i=0;i<this.allotData.length;i++){
          if(this.allotData[i].isSelected==1){
            this.checkList.push(this.allotData[i].authId)    //选中的列表点击确定之前
          }
         }
         console.log(this.checkList+"这是checkList")
      let data = {
            roleId:this.roleId,
            authIds:this.checkList
      };
      console.log(data.roleId)
        ajax(this,this.ApiConfig.userManagement.role.assignAuthorities4Role,data,(res) =>{ 
        console.log(res)
        this.allotShow = false;
      },"POST")
     },
     getUsers:function(){
     },
     handleReset:function(){
     },
     tableRowClassName:function({row, rowIndex}){
         if(rowIndex % 2 == 1){
            return "success-row"
         }
     },
     handleCurrentChange:function(val){
     this.roleDate.page = val;
     this.queryRoles();
   },

   },
   mounted:function(){
    this.queryRoles();
   }
}
</script>

<style scoped lang="postcss">
.toolbar .el-form-item .warning{
      width: 101px;
    height: 32px;
    line-height: 32px;
}
.newAdd {
  padding: 0;
}
/* .el-button--mini, .el-button--small{
  font-size: 16px;
} */
.haha{
  margin:  0 auto;
  width:532px;
  border-collapse:collapse;
  thead{
    width: 532px;
    height: 50px;
    background-color: #fef9e7;
    border:1px solid  #e6e6e6;

  font-size: 16px;
  color: #000000;
    }
  tr{
    width: 532px;
    height: 40px;
    border:1px solid  #e6e6e6;
    font-size: 14px;
    color: #000000;
  }
  td,th{
  text-align: center;
    border:1px solid  #e6e6e6;

}
input{
  width: 22px;
  height: 22px;
  background-color:transparent;
}

}

.btnSure{
  width: 255px;
  height: 50px;
  background-color: #bf9764;
  border-radius: 6px;
  border:none;
  font-size: 20px;
  color: #ffffff;
}
.exit{
  width: 150px;
  background-color: #e7e7e7;
}
.sureSure{
  width: 150px;
}
</style>

<style>
  #roleSetting {
      .el-dialog__title{
      font-size: 22px;
      color: #000000;
    }
    .el-dialog__header{
      padding: 20px 20px 0px;
    }
    .el-dialog__body{
      padding: 10px 20px;
      p{
        font-size: 18px;
        color: #000000;
      }
      .search{
        width: 200px;
        height: 30px;
        border-radius: 6px;
        border: solid 1px #d1d1d1;
      }
    }
    .el-checkbox{
      width: 100%;
    }

     .el-checkbox__input.is-checked+.el-checkbox__label{
        color:#bf9864;
      }
      .el-checkbox__input.is-checked .el-checkbox__inner, .el-checkbox__input.is-indeterminate .el-checkbox__inner{
        background-color: #bf9864;
        border-color: #bf9864;
      }
      .el-button--primary{
        background-color: #bf9864;
        border-color: #bf9864;
      }
      #newRoleDig{
        .el-dialog{
          width: 450px;
        }
        .el-dialog__title{
          font-size: 16px;
          height: 66px;
          vertical-align: top;
        }
        .el-dialog__body{
          padding: 1px 20px;
        }
        p{
          font-size: 14px;
          text-align: center;
        }
        .dialog-footer{
          text-align: center;
        }
      
      }
      #allotPower{
        .el-dialog{
          width: 450px;
        }
        .el-dialog__title{
          font-size: 16px;
          height: 66px;
          vertical-align: top;
        }
        .el-dialog__body{
          padding: 1px 20px;
          height: 400px;
          overflow-y: auto;
        }
         .dialog-footer{
          text-align: center;
          padding-bottom: 10px;
        }
        .el-checkbox{
          width: 90%;
        }

      }
      #fixRoledd{
         .el-dialog{
          width: 350px;
        }
        .el-dialog__title{
          font-size: 16px;
          height: 66px;
          vertical-align: top;
        }
        .el-dialog__body{
          padding: 1px 20px;
         }
        .dialog-footer{
          text-align: center;
          padding-bottom: 10px;

         
        }
        .el-button{
          width: 145px;
        }
        .el-form-item{
          margin-bottom: 0;
        }

      }
  }

</style>


